// Main javascript entry point
// Should handle bootstrapping/starting application

'use strict';
// import '../plugins/aos';
// import $ from 'jquery';
// import Link from '../_modules/link/link';
const btn = document.querySelectorAll('.btn');
const btnBurger = document.querySelectorAll('.btn--burger');
const wrapper = document.querySelector('.wrapper');
const mainMenu = document.querySelector('.page-header__main-menu');

$(() => {
  const personsNav = document.querySelector('.persons-list');
  const $slider = $('.person-info__list');
  let indexCurrentSlide = 0;

  const toggleClassPersonsNavLink = link => {
    if (link.classList.contains('persons-list__item--active')) return;
    let linkActive = personsNav.querySelector('.persons-list__item--active');
    linkActive.classList.remove('persons-list__item--active');
    link.classList.add('persons-list__item--active');
  };

  $slider.slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true,
    fade: true,
    responsive: [
      {
        breakpoint: 768,
        settings: {
          arrows: false
        }
      }
      // You can unslick at a given breakpoint now by adding:
      // settings: "unslick"
      // instead of a settings object
    ]
    // rtl: true
  });

  $slider.on('afterChange', evt => {
    indexCurrentSlide = $slider.slick('slickCurrentSlide');
    toggleClassPersonsNavLink(personsNav.querySelectorAll('.persons-list__item')[indexCurrentSlide]);
  });

  const toggleSliderImg = link => {
    const indexImg = parseInt(link.getAttribute('href'));
    $('.person-info__list').slick('slickGoTo', indexImg);
  };

  const onClickPersonsNavLink = evt => {
    if (!evt.target.closest('.persons-list__item')) return;
    evt.preventDefault();
    let link = evt.target.closest('.persons-list__item');
    toggleClassPersonsNavLink(link);
    toggleSliderImg(link);
  };

  personsNav.addEventListener('click', onClickPersonsNavLink);

});

function animateTriangle(div, width, height, offset) {
  $(window).scroll(function () {
    if ($(window).width() > 1025) {
      div.animate({
        width: width,
        height: height
      }, 1000);
    }
    else if ($(window).width() > 768) {
      div.animate({
        width: width,
        height: height
      }, 1000);
    }
  });
};

$(window).scroll(function () {
  if ($(this).scrollTop() > 30) {
    animateTriangle($('.preview-event__laser'), 312, 376);
  }
  if ($(this).scrollTop() > 600) {
    animateTriangle($('.think__laser'), 822, 633);
  }
  if ($(this).scrollTop() > 55 && $(window).width() < 1025) {
    document.querySelector('.page-header__navs-mobile').classList.add('page-header__navs-mobile--active');
  }
  else {
    document.querySelector('.page-header__navs-mobile').classList.remove('page-header__navs-mobile--active');
  }
});

[...btnBurger].map(item => {
  item.addEventListener('click', (e) => {
    mainMenu.classList.toggle('page-header__main-menu--active-mobile');
    wrapper.classList.toggle('wrapper--overlay');
    [...btn].map(button => {
      button.classList.toggle('burger--active');
    });
  });
});


// anchor

const anchors = document.querySelectorAll('a[href*="#"]');

[...anchors].map(anchor => {
  anchor.addEventListener('click', e => {
    e.preventDefault();
    const blockID = anchor.getAttribute('href');
    document.querySelector('' + blockID).scrollIntoView({
      behavior: 'smooth',
      block: 'start'
    });
  });
});


// counter

let counter = () => {
  const ticketNumbers = document.querySelectorAll('.buy-ticket__num');
  const ticketValues = document.querySelectorAll('.buy-ticket__value');
  const plusQtys = document.querySelectorAll('.buy-ticket__plus-qty');
  const minusQtys = document.querySelectorAll('.buy-ticket__minus-qty');

  [...plusQtys].map(plusQty => {
    plusQty.onclick = () => {
      [...ticketNumbers].map(ticketNumber => {
        let count = Number(ticketNumber.innerHTML);
        ticketNumber.innerHTML = ++count;
        Number(ticketNumber.innerHTML) >= 2 && [...minusQtys].map(minusQty => {
          minusQty.removeAttribute('disabled');
        });
      });
      [...ticketValues].map(ticketValue => {
        let value = Number(ticketValue.innerHTML);
        ticketValue.innerHTML = value += 500;
      });
    };
  });

  [...minusQtys].map(minusQty => {
    minusQty.onclick = () => {
      [...ticketNumbers].map(ticketNumber => {
        let count = Number(ticketNumber.innerHTML);
        Number(ticketNumber.innerHTML) > 1
          && (ticketNumber.innerHTML = --count);
        Number(ticketNumber.innerHTML) == 1 && [...minusQtys].map(minusQty => {
          minusQty.setAttribute('disabled', true);
        });
      });
      [...ticketValues].map(ticketValue => {
        let value = Number(ticketValue.innerHTML);
        ticketValue.innerHTML = value -= 500;
      });
    };
  });
};

counter();

// PopUp

let modal = document.querySelector('.popup');
let playTicket = document.querySelectorAll('.btn--pay-ticket');
let closeButton = document.querySelector('.popup__close');

const toggleModal = () => {
  modal.classList.toggle('popup--show');
};

const windowOnClick = event => {
  event.target === modal && toggleModal();
};

[...playTicket].map(button => {
  button.addEventListener('click', toggleModal);
});

closeButton.addEventListener('click', toggleModal);
window.addEventListener('click', windowOnClick);
